import React, { useState, useRef, useCallback } from "react";
import { WebcamContainer } from "./WebCam";
import BrowserDetection from 'react-browser-detection';
import './App.css';

const browserHandler = {
  safari: () => <WebcamContainer mimeType={"video/mp4"} />,
  firefox: () => <WebcamContainer badBrowser={true} mimeType={"video/mp4"} />,
  default: () => <WebcamContainer mimeType={"video/webm"} />
};

function App() {
  return (
    <BrowserDetection>
      { browserHandler }
    </BrowserDetection>
  )
}

export default App;
