import React, { useEffect, useCallback, useRef, useState } from "react";
import Webcam from "react-webcam";
import GooglyEyes from "./GooglyEyes";
import Preview from "./Preview";

const FACE_MASK = '/faceCenter.png';

const VIDEO_SIZE = {
  width: 300,
  height: 300
};
export default function Recorder({
  mimeType,
  recorderOpen,
  setRecorderOpen,
  recording,
  setRecording,
  showWebCam,
  setShowWebCam,
  uploading,
  setUploading,
  uploaded,
  setUploaded,
  recordedChunks,
  setRecordedChunks,
  webcamRef,
  mediaRecorderRef
}) {
  useEffect(() => {
    if (recording) {
      setTimeout(() => {
        mediaRecorderRef.current.stop();
        setRecording(false);
        setShowWebCam(false);
      }, 1000);
    }
  }, [recording]);

  useEffect(() => {
    if (!recording && recordedChunks.length) {
      const blob = new Blob(recordedChunks, { type: mimeType });
      const url = URL.createObjectURL(blob);
      const video = document.createElement("video");
      video.src = url;
      video.classList.add("recordedContainer");
      document.body.appendChild(video);
      video.loop = true;
      video.playsInline = true;
      video.muted = true;
      video.autoPlay = true;
      video.load();
      video.play();
    }
  }, [recording, recordedChunks]);

  return (
    <>
      { showWebCam &&
        <Webcam
          className="playerContainer"
          audio={false}
          ref={webcamRef}
          muted={true}
          mirrored={true}
          videoConstraints={VIDEO_SIZE}
          width={VIDEO_SIZE.width}
          height={VIDEO_SIZE.height}
          style={{ objectFit: "cover" }} />
      }
      <GooglyEyes recording={recording} recordedChunks={recordedChunks} />
    </>
  );
}
