import { useEffect, useState } from "react";
import shuffle from 'just-shuffle';

const HOST_NAME = 'https://storage.googleapis.com/everyoneeverywhere.appspot.com/';

function loadAsset(video) {
  return new Promise((resolve, reject) => {
    const xhrReq = new XMLHttpRequest();
    xhrReq.open('GET', video, true);
    xhrReq.responseType = 'blob';

    xhrReq.onload = function() {
      if (this.status === 200) {
        resolve()
      }
    }
    xhrReq.onerror = function() {
      reject();
    }
    xhrReq.send();
  });
}

export default function Player({ interval, myFaceId }) {
  const [loading, setLoading] = useState(false);
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    async function _loadAsset() {
      setLoading(true);
      await loadAsset('/everyone.mp4');
      setLoaded(true);
    }
    if (!loading) {
      _loadAsset();
    }
  }, [loading]);

  if (!loaded) {
    return (
      <div id="loadingContainer">
        <span>loading...</span>
      </div>
    )
  }
  return (
    <div className="playerContainer">
      <video className="playerItem currentVideo" src="/everyone.mp4" type="video/mp4" height="300" muted autoPlay playsInline loop></video>
    </div>
  );
}
