import React, { useEffect, useState, useRef } from "react";
import queryString from "query-string";
import Player from "./Player";
import Recorder from "./Recorder";
import Footer from "./Footer";

// https://overreacted.io/making-setinterval-declarative-with-react-hooks/
function useInterval(callback, delay) {
  const savedCallback = useRef();

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}
const SMUSHED_UUID_REGEX = /^[0-9a-f]{8}[0-9a-f]{4}[0-9a-f]{4}[0-9a-f]{4}[0-9a-f]{12}$/
export const WebcamContainer = ({ badBrowser, mimeType, location }) => {
  const webcamRef = useRef(null);
  const mediaRecorderRef = useRef(null);
  const [myFaceId, setMyFaceId] = useState(null);
  const [interval, setInterval] = useState(0);
  const [recorderOpen, setRecorderOpen] = useState(false);
  const [recording, setRecording] = useState(false);
  const [showWebCam, setShowWebCam] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [uploaded, setUploaded] = useState(false);
  const [recordedChunks, setRecordedChunks] = useState([]);

  useInterval(() => {
    setInterval(interval + 1);
  }, 100);

  useEffect(() => {
    if (window.location.pathname.startsWith('/w/')) {
      const faceId = window.location.pathname.split('/w/')[1];
      const trimmedFaceId = faceId.replaceAll('/', '');
      if (trimmedFaceId && SMUSHED_UUID_REGEX.test(trimmedFaceId)) {
        setMyFaceId(trimmedFaceId);
      }
    }
  }, [myFaceId]);
  return (
    <>
      <div id="mainText">
        <div id="title"><div>Everyone</div><div>Everywhere</div><div>All At Once</div></div>
        { !recorderOpen &&
          <>
            <div id="subTitle">Inspired by the film <em><a target="_blank" href="https://en.wikipedia.org/wiki/Everything_Everywhere_All_at_Once">Everything Everywhere All At Once</a></em>, I made this project for fans of the film to add their face for a composition. The project is closed now. Be kind and don't give into the everything bagel. </div>
          </>
        }
        { recorderOpen && !uploaded &&
          <div id="subTitleInstructions">
            <strong>Instructions:</strong>
            <div>1. Grant permission to access your camera.</div>
            <div>2. Align your eyes behind the googly eyes.</div>
            <div>3. <strong>Look into your camera</strong></div>
            <div>4. Press 'Start'.</div>
            <div>5. Upload your video!</div>
          </div>
        }
        { recorderOpen && uploaded &&
          <div id="subTitle">
            <div><strong>Uploaded! </strong></div>
            <div>You'll see your face with everyone. If you like this project, let me know! </div>
            <div><a target="_blank" href="https://twitter.com/justinprojects">@justinprojects</a></div>
          </div>
        }
      </div>
      { !recorderOpen && <Player interval={interval} myFaceId={myFaceId} /> }
      { recorderOpen &&
        <Recorder
          mimeType={mimeType}
          webcamRef={webcamRef}
          mediaRecorderRef={mediaRecorderRef}

          recorderOpen={recorderOpen}
          recording={recording}
          showWebCam={showWebCam}
          uploading={uploading}
          uploaded={uploaded}
          recordedChunks={recordedChunks}

          setRecorderOpen={setRecorderOpen}
          setRecording={setRecording}
          setShowWebCam={setShowWebCam}
          setUploading={setUploading}
          setUploaded={setUploaded}
          setRecordedChunks={setRecordedChunks}
        />
      }
      <Footer interval={interval} />
    </>
  );
};
